import React, {useCallback, useEffect,useState } from 'react'
import Button from "../Button/Button";
import { inject, observer } from 'mobx-react';
import version from '../../model/Version'
import ApiProxy from "../../model/ApiProxy";
import {openConfigListCache} from "../../lib/web3js/index";
import { isLP } from '../../utils/utils';

function TransactionButton({lang,wallet,pool = {},bTokenId,bToken,icon,label,onClick,isReady,disabled = true,statusCallback, alert,...restProps}){
  const [enabled, setEnabled] = useState(true)
  const [btnText,setBtnText] = useState(label)
  const [image, setImage] = useState(icon)

  const isApprove = useCallback(async (wallet,pool) => {
    let result
    if(wallet.detail && pool.address){
      if (version.isOpen) {
        await openConfigListCache.update()
      }
      if (isLP(pool.address)) {
        result = await ApiProxy.request('isLpUnlocked', [wallet.detail.chainId, pool.address, wallet.detail.account])
      } else {
        const params = [wallet.detail.chainId, pool.address, wallet.detail.account,bToken || bTokenId]
        result = await ApiProxy.request('isUnlocked', params)
      }
    }
    return result;
  },[bToken,bTokenId])

  const approve = useCallback(async () => {
    if (wallet && pool) {
      let approved;
      if (version.isOpen) {
        await openConfigListCache.update()
      }
      if (isLP(pool.address)) {
        approved = await ApiProxy.request('unlockLp', [wallet.detail.chainId, pool.address,wallet.detail.account]);
      } else {
        let params = [wallet.detail.chainId, pool.address, wallet.detail.account,bToken || bTokenId]
        approved = await ApiProxy.request('unlock', params);
      }
      return approved
    }
  },[pool,wallet,bTokenId,bToken])

  const click = async () => {
    if(pool && wallet && wallet.detail){
      if(!wallet.isConnected()) {
        setImage(null)
        return wallet.connect()
      } else if(await isApprove(wallet,pool)) {
        setImage(icon)
        return onClick();
      } else {
        setImage(null)
        let res = await approve()
        if(res){
          alert && alert.success('Approve success')
          statusCallback && statusCallback(false)
        }else{
          alert && alert.error('Approve failed')
          statusCallback && statusCallback(true)
        }
        return res;
      }
    }
  }

  useEffect(async () => {
    if(pool && wallet.detail && wallet.detail.account && pool.bTokens && lang){
      if(!wallet.isConnected()) {
        setBtnText(lang['connect-wallet'])
        setImage(null)
      } else if(await isApprove(wallet,pool)) {
        setBtnText(label)
        setImage(icon)
        statusCallback && statusCallback(false)
      } else {
        setBtnText(lang['approve'])
        setImage(null)
        statusCallback && statusCallback(true)
      }
    }
  },[pool,wallet,lang,bToken])

  useEffect(()=>{
    setEnabled(disabled)
  },[disabled])

  useEffect(() => {
    if(wallet && pool && pool.address){
      wallet.setAddress(pool.address);
      if(pool.isV3 || pool.isV3Lite){
        wallet.setBToken(bToken)
      } else {
        wallet.setBTokenId(bTokenId)
      }
    }
  },[wallet,pool,bTokenId,bToken])
  return(
    <Button {...restProps} disabled={!enabled} label={btnText} onClick={click} icon={image}/>
  )
}
export default inject('wallet')(observer(TransactionButton))